body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }

  .heading {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 54px;
    font-weight: bold;
    line-height: 68px;
    height: 68px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 68px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .game-info {
    margin-left: 30px;
    font-size: 30px;
  }

  .go-to-btn {
    padding: 5px;
    font-size: 15px;
  }

/* Tablet/Desktop Styles */
  @media only screen and (min-width: 768px) {
    .game {
      display: flex;
      flex-direction: row;
      justify-content: center;
    }
  }